@import url(https://fonts.googleapis.com/css2?family=Dosis:wght@200;300;400;500;600;700;800&display=swap);
*{margin:0;box-sizing:border-box}body{font-size:1rem;font-family:"Dosis", sans-serif;height:100vh;background-color:black}.App{position:absolute;top:50%;left:50%;transform:translate(-50%, -50%);text-align:center;color:white;width:100%;padding:20px}.App__offer{padding:20px 0}.button{background:linear-gradient(45deg, #7750d9 30%, #53ddff 90%);box-shadow:0 3px 5px 2px rgba(66,220,219,0.2);height:48px;color:#fff !important;width:160px}

.clock__watch{padding:10px 0;font-size:24px}.clock__time{color:white;font-size:3em;text-align:center;animation:neonGlow 2s infinite alternate cubic-bezier(0.45, 0.03, 0.51, 0.95)}@media (max-width: 500px){.clock__time{font-size:2.5em}}.clock__title{font-size:14px}.clock__text{font-size:20px}@keyframes neonGlow{0%{text-shadow:0 0 10px rgba(255,255,255,0.8),0 0 20px rgba(255,255,255,0.8),0 0 22px rgba(255,255,255,0.8),0 0 40px rgba(66,220,219,0.8),0 0 60px rgba(66,220,219,0.8),0 0 80px rgba(66,220,219,0.5),0 0 100px rgba(66,220,219,0.5),0 0 140px rgba(66,220,219,0.5),0 0 200px rgba(66,220,219,0.5)}100%{text-shadow:0 0 2px rgba(255,255,255,0.8),0 0 8px rgba(255,255,255,0.8),0 0 10px rgba(255,255,255,0.8),0 0 20px rgba(66,220,219,0.8),0 0 30px rgba(66,220,219,0.8),0 0 40px rgba(66,220,219,0.8),0 0 50px rgba(66,220,219,0.5),0 0 80px rgba(66,220,219,0.5)}}

